// Masks that select the SBOX input
const SBOX_MASK = [
  0xf8000001, 0x1f800000, 0x01f80000, 0x001f8000,
  0x0001f800, 0x00001f80, 0x000001f8, 0x8000001f,
];

// Swap bits across the left and right words
function exchangeLR(this: DESAlgo, offset: number, mask: number): void {
  if (this._lBlock === undefined || this._rBlock === undefined) {
    throw new Error('Block values not initialized');
  }
  const t = ((this._lBlock >>> offset) ^ this._rBlock) & mask;
  this._rBlock ^= t;
  this._lBlock ^= t << offset;
}

function exchangeRL(this: DESAlgo, offset: number, mask: number): void {
  if (this._lBlock === undefined || this._rBlock === undefined) {
    throw new Error('Block values not initialized');
  }
  const t = ((this._rBlock >>> offset) ^ this._lBlock) & mask;
  this._lBlock ^= t;
  this._rBlock ^= t << offset;
}

/**
 * DES block cipher algorithm.
 */
export class DESAlgo extends BlockCipher {
  /** Key size in 32-bit words */
  static keySize = 64 / 32;
  
  /** IV size in 32-bit words */
  static ivSize = 64 / 32;
  
  /** Block size in 32-bit words */
  declare blockSize: number;
  
  /** Subkeys for encryption */
  private _subKeys!: number[][];
  
  /** Inverse subkeys for decryption */
  private _invSubKeys!: number[][];
  
  /** Left block for processing */
  protected _lBlock!: number;
  
  /** Right block for processing */
  protected _rBlock!: number;

  constructor(xformMode: number, key: WordArray, cfg?: CipherCfg) {
    super(xformMode, key, cfg);
    
    // blockSize is an instance field and should set in constructor.
    // Both DESAlgo and TripleDESAlgo.
    this.blockSize = 64 / 32;
  }

  protected _doReset(): void {
    // Shortcuts
    const key = this._key;
    const keyWords = key.words;

    // Select 56 bits according to PC1
    const keyBits: number[] = [];
    for (let i = 0; i < 56; i += 1) {
      const keyBitPos = PC1[i] - 1;
      keyBits[i] = (keyWords[keyBitPos >>> 5] >>> (31 - (keyBitPos % 32))) & 1;
    }

    // Assemble 16 subkeys
    this._subKeys = [];
    const subKeys = this._subKeys;
    for (let nSubKey = 0; nSubKey < 16; nSubKey += 1) {
      // Create subkey
      subKeys[nSubKey] = [];
      const subKey = subKeys[nSubKey];

      // Shortcut
      const bitShift = BIT_SHIFTS[nSubKey];

      // Select 48 bits according to PC2
      for (let i = 0; i < 24; i += 1) {
        // Select from the left 28 key bits
        subKey[(i / 6) | 0] |= keyBits[((PC2[i] - 1) + bitShift) % 28] << (31 - (i % 6));

        // Select from the right 28 key bits
        subKey[4 + ((i / 6) | 0)]
          |= keyBits[28 + (((PC2[i + 24] - 1) + bitShift) % 28)]
          << (31 - (i % 6));
      }

      // Since each subkey is applied to an expanded 32-bit input,
      // the subkey can be broken into 8 values scaled to 32-bits,
      // which allows the key to be used without expansion
      subKey[0] = (subKey[0] << 1) | (subKey[0] >>> 31);
      for (let i = 1; i < 7; i += 1) {
        subKey[i] >>>= ((i - 1) * 4 + 3);
      }
      subKey[7] = (subKey[7] << 5) | (subKey[7] >>> 27);
    }

    // Compute inverse subkeys
    this._invSubKeys = [];
    const invSubKeys = this._invSubKeys;
    for (let i = 0; i < 16; i += 1) {
      invSubKeys[i] = subKeys[15 - i];
    }
  }

  encryptBlock(M: number[], offset: number): void {
    this._doCryptBlock(M, offset, this._subKeys!);
  }

  decryptBlock(M: number[], offset: number): void {
    this._doCryptBlock(M, offset, this._invSubKeys!);
  }

  private _doCryptBlock(M: number[], offset: number, subKeys: number[][]): void {
    const _M = M;

    // Get input
    this._lBlock = M[offset];
    this._rBlock = M[offset + 1];

    // Initial permutation
    exchangeLR.call(this, 4, 0x0f0f0f0f);
    exchangeLR.call(this, 16, 0x0000ffff);
    exchangeRL.call(this, 2, 0x33333333);
    exchangeRL.call(this, 8, 0x00ff00ff);
    exchangeLR.call(this, 1, 0x55555555);

    // Rounds
    for (let round = 0; round < 16; round += 1) {
      // Shortcuts
      const subKey = subKeys[round];
      const lBlock: number = this._lBlock!;
      const rBlock: number = this._rBlock!;

      // Feistel function
      let f = 0;
      for (let i = 0; i < 8; i += 1) {
        f |= SBOX_P[i][((rBlock ^ subKey[i]) & SBOX_MASK[i]) >>> 0];
      }
      this._lBlock = rBlock;
      this._rBlock = lBlock ^ f;
    }

    // Undo swap from last round
    const t = this._lBlock!;
    this._lBlock = this._rBlock;
    this._rBlock = t;

    // Final permutation
    exchangeLR.call(this, 1, 0x55555555);
    exchangeRL.call(this, 8, 0x00ff00ff);
    exchangeRL.call(this, 2, 0x33333333);
    exchangeLR.call(this, 16, 0x0000ffff);
    exchangeLR.call(this, 4, 0x0f0f0f0f);

    // Set output
    _M[offset] = this._lBlock!;
    _M[offset + 1] = this._rBlock!;
  }
}

/**
 * Shortcut functions to the cipher's object interface.
 *
 * @example
 *
 *     var ciphertext = DES.encrypt(message, key, cfg);
 *     var plaintext  = DES.decrypt(ciphertext, key, cfg);
 */
export const DES: CipherObj = BlockCipher._createHelper(DESAlgo);

/**
 * Triple-DES block cipher algorithm.
 */
export class TripleDESAlgo extends BlockCipher {
  /** Key size in 32-bit words */
  static keySize = 192 / 32;
  
  /** IV size in 32-bit words */
  static ivSize = 64 / 32;
  
  /** Block size in 32-bit words */
  declare blockSize: number;
  
  /** First DES instance */
  private _des1!: DESAlgo;
  
  /** Second DES instance */
  private _des2!: DESAlgo;
  
  /** Third DES instance */
  private _des3!: DESAlgo;

  protected _doReset(): void {
    // Shortcuts
    const key = this._key;
    const keyWords = key.words;
    // Make sure the key length is valid (64, 128 or >= 192 bit)
    if (keyWords.length !== 2 && keyWords.length !== 4 && keyWords.length < 6) {
      throw new Error('Invalid key length - 3DES requires the key length to be 64, 128, 192 or >192.');
    }

    // Extend the key according to the keying options defined in 3DES standard
    const key1 = keyWords.slice(0, 2);
    const key2 = keyWords.length < 4 ? keyWords.slice(0, 2) : keyWords.slice(2, 4);
    const key3 = keyWords.length < 6 ? keyWords.slice(0, 2) : keyWords.slice(4, 6);

    // Create DES instances
    this._des1 = DESAlgo.createEncryptor(WordArray.create(key1)) as DESAlgo;
    this._des2 = DESAlgo.createEncryptor(WordArray.create(key2)) as DESAlgo;
    this._des3 = DESAlgo.createEncryptor(WordArray.create(key3)) as DESAlgo;
  }

  encryptBlock(M: number[], offset: number): void {
    this._des1!.encryptBlock(M, offset);
    this._des2!.decryptBlock(M, offset);
    this._des3!.encryptBlock(M, offset);
  }

  decryptBlock(M: number[], offset: number): void {
    this._des3!.decryptBlock(M, offset);
    this._des2!.encryptBlock(M, offset);
    this._des1!.decryptBlock(M, offset);
  }
}

/**
 * Shortcut functions to the cipher's object interface.
 *
 * @example
 *
 *     var ciphertext = TripleDES.encrypt(message, key, cfg);
 *     var plaintext  = TripleDES.decrypt(ciphertext, key, cfg);
 */
export const TripleDES: CipherObj = BlockCipher._createHelper(TripleDESAlgo);